import React, { Component } from 'react';
import './AboutMe.css'
import { FiGithub, FiLinkedin, FiSend, FiYoutube } from 'react-icons/fi';
import Img1 from '../img/face1.jpg'

class AboutMe extends Component {
    state = {}

    // backgndImageStyle = {

    //     backgroundImage: `url(${Img1})`,
    //     backgroundPosition: 'center',
    //     backgroundSize: 'cover',
    //     backgroundRepeat: 'no-repeat'


    // }



    render() {
        return (
            <div className='aboutMeContainer'  >
                <div className='mediaContainer'  >
                    <div className="image2"><img src={Img1} className='twoFaces' alt="" /></div>
                    <div className="gitHub icons"> <a rel="noopener noreferrer" href="https://github.com/waterbreathing/" target="_blank"> <FiGithub /> </a></div>
                    <div className="linkedIn icons"> <a rel="noopener noreferrer" href="https://www.linkedin.com/in/michal-d-9b35b5191/" target="_blank"><FiLinkedin /></a></div>
                    <div className="mailMe icons"> <a rel="noopener noreferrer" href="mailto:mdzik@waterbreathing.com" target="_blank"><FiSend /></a></div>
                    <div className="youTube icons"> <a rel="noopener noreferrer" href="https://www.youtube.com/watch?v=zGIp21wPNN0" target="_blank"><FiYoutube /></a></div>

                    <div className='aboutMe' >
                        I am a Front-end developer and I'm looking for my first challenge. I value good, honest and intuitive design as well as reliable operation of websites. I have strong fundamentals of JS, ES6, React.js, JQuery, SASS, Flexbox, Grid, Git, OOP, RWD, HTML, CSS and a sense of design and I'm able to use them in junior-level tasks.
    I would like to join a creative team and increase my skills in Front end with an emphasis on JS and React.js. I'm currently exploring node.js, Express, mongoDB and React, React and more React. My personal interests include art, literature, photography and music composition. You can listen to my music by clicking on youTube icon.
                    </div>
                    {/* <img src={Img1} alt="background" /> */}
                </div>
                <div className='dialogue' >
                    <p>Please tell me a few words about yourself.</p>
                    <p>Life is too short to stand still. This banal sounding statement will be an empty phrase for some, but for me it is a philosophy of life. I am constantly looking for new challenges. In addition to medicine, with which I am professionally associated, I am extremely interested in new technologies and changes that follow them. Therefore I devoted a lot of time to  intensive IT learning and multiple courses on programming and Front-end engineering.</p>
                    <p>Why do you find new technologies fascinating?</p>
                    <p>The world is changed by technologies we didn't even dream about 20-30 years ago. I find it fascinating not only in technical, but also in social and information-wise terms. I would like to personally explore, follow and even contribute to these changes.</p>
                    <p>What exactly would you like to do?</p>
                    <p>I started learning IT from HTML, CSS, JavaScript and React, which results in this website. I like the philosophy offered by programming languages and enormous intellectual challenge it makes. I plan to learn the backend development and databases. I love design and I think I have a UX feel. Perhaps I will prove to be a good programmer, and maybe I will prove to be valuable colleague for the team of designers and UX? Time and especially practice will show in which technologies I will feel best. One thing is certain, if you give me a chance, you'll get a very good employee.</p>
                    {/* <p>Do you think that you will manage in a completely new industry? </p>
                    <p>Absolutely yes. I often had to learn new difficult things, it always went quite smoothly. My professional career went well and I had some achievements. More importantly, I've never had conflicts with my colleagues or clients. </p> */}
                    <p>What are your strengths? </p>
                    <p>Soft skills. Communication. I am diligent and conscientious. Always get the work done, almost never late at work. I’m also easy-going and creative, I often have a lot of ideas. I think I’m a fast learner and I can also share my knowledge.</p>
                    <p>What are your weaknesses then?</p>
                    <p>Well, let me think… I didn't watch “The Game of Thrones’ so we can’t talk about the story. Maybe this?</p>
                    <p>Why should we hire you?</p>
                    <p>I think I'm more stable than younger colleagues and less likely to leave a job. With more life experience comes better understanding of people and the world in general. Although in another industry, I am a proven reliable employee, and this is a matter of personality and not the workplace.</p>
                </div>
            </div>
        );
    }
}

export default AboutMe













